import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import "./Detail.scss";

// 상품 상세페이지 /detail
function GoodDetail(props) {
  /**
   * useEffect(callback, [condition]) : 컴포넌트가 렌더링 될 때마다(create, update) 콜백함수 실행
   * callback 함수 작성 후, 두 번째 파라미터에 대괄호 안에 변수나 함수를 넣으면
   * 해당 데이터가 변경될 때만 useEffect가 실행되게 할 수 있다.(여러 개 넣을 수 있음)
   * useEffect()는 여러 개 사용이 가능하며, 위에서부터 순서대로 실행된다.
   */
  let history = useHistory();
  let { id } = useParams();
  let index =
    props.goods.findIndex((good) => good.id == id) === -1
      ? 0
      : props.goods.findIndex((good) => good.id == id);
  let [alert, alertToggle] = useState(true);
  let [temp, tempModify] = useState();

  useEffect(() => {
    let timer = setTimeout(() => {
      alertToggle(false);
    }, 2000);

    // 해당 component가 재렌더링 될 때(죽을 때) 실행할 함수
    return () => {
      console.log(timer);
      clearTimeout(timer);
    };
  }, [alert]);

  return (
    <div className="container">
      <div className="salmon">
        <h3>Product</h3>
      </div>
      {alert ? (
        <div className="alert">
          <p>재고가 얼마 남지 않았습니다!</p>
        </div>
      ) : null}

      <div className="row">
        <div className="col-md-6">
          <img src={props.goods[index].img} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{props.goods[index].title}</h4>
          <p>{props.goods[index].content}</p>
          <p>{props.goods[index].price} 원</p>
          <Info inven={props.inven[index]}></Info>
          <button
            className="btn btn-danger"
            onClick={() => {
              let invenCopy = [...props.inven];
              invenCopy[index] -= 1;
              props.invenModify(invenCopy);
            }}
          >
            주문하기
          </button>
          &nbsp;
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Info(props) {
  return <p>남은 재고 : {props.inven} 개</p>;
}

export default GoodDetail;
